// src/components/FAQSection.jsx

import React from 'react';

const faqData = {
  english: [
    {
      question: 'What services does Zocial Painter offer?',
      answer:
        'Zocial Painter connects you instantly with expert painters and plumbers across Kathmandu Valley for wall painting, repairs, and plumbing solutions.',
    },
    {
      question: 'Do I need to create an account to use the service?',
      answer:
        'No registration required! Just pick your service, location, and connect directly through our interactive map or forms.',
    },
    {
      question: 'Where is this service available?',
      answer:
        'Currently serving Kathmandu, Lalitpur, and Bhaktapur with plans to expand soon.',
    },
    {
      question: 'How can I contact support?',
      answer:
        'Reach us anytime by phone at 9851151294. Our team is ready to assist you promptly.',
    },
    {
      question: 'Can painters and plumbers mark their availability?',
      answer:
        'Yes! Service providers can update their live availability on the map so customers can find help nearby instantly.',
    },
  ],
  nepali: [
    {
      question: 'Zocial Painter के सेवा दिन्छ?',
      answer:
        'Zocial Painter तपाईंलाई काठमाडौं उपत्यकाका विज्ञ चित्रकार र प्लम्बरहरूसँग जोड्छ — पेन्टिङ, मर्मत र प्लम्बिङ समाधानका लागि।',
    },
    {
      question: 'सेवा प्रयोग गर्न खाता बनाउनु पर्छ?',
      answer:
        'खाता बनाउन आवश्यक छैन! तपाईंले हाम्रो नक्सा वा फारमबाट सिधै सेवा छनौट गरी सम्पर्क गर्न सक्नुहुन्छ।',
    },
    {
      question: 'यो सेवा कहाँ उपलब्ध छ?',
      answer:
        'हाल यो सेवा काठमाडौं, ललितपुर र भक्तपुरमा उपलब्ध छ र विस्तारको योजना छ।',
    },
    {
      question: 'म कसरी सहयोग सम्पर्क गर्न सक्छु?',
      answer:
        'कुनै पनि समयमा ९८५११५१२९४ मा फोन गर्नुहोस्। हाम्रो टिम तत्पर छ तपाईंलाई सहयोग गर्न।',
    },
    {
      question: 'चित्रकार र प्लम्बरहरूले आफ्नो उपलब्धता देखाउन सक्छन्?',
      answer:
        'हो! सेवा प्रदायकहरूले आफ्नो उपलब्धता नक्सामा अपडेट गर्न सक्छन् जसले ग्राहकलाई छिटो नजिकको सहयोग खोज्न सजिलो हुन्छ।',
    },
  ],
};

export default function FAQSection({ language }) {
  const faqs = faqData[language] || faqData.english;

  return (
    <section className="max-w-3xl mx-auto my-12 px-4">
      <h2 className="text-3xl font-bold mb-6 text-center">
        {language === 'english' ? 'Frequently Asked Questions' : 'प्रायः सोधिने प्रश्नहरू'}
      </h2>
      <div className="space-y-6">
        {faqs.map(({ question, answer }, i) => (
          <div key={i} className="border border-gray-300 rounded p-4 shadow-sm">
            <h3 className="font-semibold text-lg mb-2">{question}</h3>
            <p className="text-gray-700">{answer}</p>
          </div>
        ))}
      </div>
    </section>
  );
}
